.container__center {
    display: flex;
    width: 100%;
    justify-content: center;
    position: fixed;
    bottom: 2rem;
}

nav {
    background: rgba(0, 0, 0, 0.3);
    width: max-content;
    display: flex;
    padding: .7rem 1.7rem;
    z-index: 10000;
    gap: .8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

nav a {
    background: transparent;
    padding: .9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
    text-decoration: none;
    transition: var(--transition);
}

nav a:hover {
    background: rgba(0, 0, 0, .3);
}

nav a.active {
    background: var(--color-bg);
    color: var(--color-white);
}