footer {
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: .9rem;
    margin-top: 7rem;
}

footer a {
    color: var(--color-bg);
    font-weight: bold;
}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer__socials {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: .4rem;
    font-size: 1.4rem;
}

.footer__socials a {
    transition: var(--transition);
    background: var(--color-bg);
    color: var(--color-white);
    padding: .4rem;
    border-radius: .7rem;
    display: flex;
    border: 1px solid transparent;
}

.footer__socials a:hover {
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}

.footer__copyright {
    margin-bottom: 4rem;
    color: var(--color-bg);   
}

@media screen and (max-width:1200px) {
    footer {
        margin-top: 30rem;
    }
}

@media screen  and (max-width:600px) {
    footer {
        margin-top: 30em;
    }
    .permalinks {
        flex-direction: column;
        gap: 1.5rem;
    }

    .footer__socials {
        margin-bottom: 2.6rem;
    }
    
}