h2 {
    padding-top: 5rem;
}

.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15;

}

.about__me {
    width: 100%;
    aspect-ratio: 1/1;
    z-index: -2;
    border-radius: 2rem;
    background: radial-gradient(var(--color-primary) 40%, transparent, transparent );
    display: grid;
    place-items: center;
}

.about__me-image {
    border-radius: 2rem;
    z-index: -1;
    height: 100%;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}

.about__me-image:hover {
    transform: rotate(0deg);
}

.about__me-user-image {
    max-height: 100%;
}

/* ========= ABOUT CARDS ========== */

.about__cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
}

.about__card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon {
    color: var(--color-primary);
    font-size: 3rem;
    margin-bottom: .2rem;
}

.about__card h5 {
    font-size: 1.2rem;
}

.about__card small {
    font-size: 1rem;
    color: var(--color-light);
}

.about__content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}

.about__contact {
    display: flex;
    justify-content: center;
}

@media screen and (max-width:1024px){
    .about__container {
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about__me {
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about__content p {
        margin: 1rem 0 1.5rem;
    }
}

@media screen and (max-width:600px){
    .about__me {
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about__cards {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .about__content {
        text-align: center;
    }

    .about__content p {
        margin: 1.5rem 0;
    }
}